@import url("https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Roboto+Slab:wght@600&display=swap");
@import "~react-image-gallery/styles/scss/image-gallery.scss";

:root {
  /* ----- COLOR VARIABLES ----- */
  --pri1: white;
  --pri2: #3d3b3c;
  --pri3: #524e50;

  --sec1: #014751;
  --sec1_2: #002126;
  --sec2: #00662c;
  --sec3: #01a84e;
  --sec4: #044454;

  --black-transparent: rgba(27, 27, 28, 0.8);
  --black-grey: #2d2e32;

  --bs-gutter-x: 0rem !important;

  /* -----  ----- */
}

body {
  background-color: var(--sec1) !important;
  font-family: "Lato", sans-serif;
  font-weight: 300;
  color: var(--pri1);
}

b {
  font-weight: 700;
}

.container-fluid {
  padding: 0 !important;
}

.row {
  --bs-gutter-x: 0rem !important;
}

h1,
h2,
h3 {
  font-family: "Roboto Slab", serif;
}

a {
  text-decoration: none !important;
}

a:hover {
  color: var(--sec4);
}

section {
  background-color: var(--sec1);
  // padding-top: 75px !important;
}

.separador {
  background-color: var(--sec2);
  text-align: center;
  padding: 5px;
  padding-top: 7rem;
  padding-bottom: 7rem;
  font-size: 20px;
}

/* =================== MEDIA QUERIES (MEDIUM DEVICES) =================== */
@media screen and (min-width: 1024px) {
  body::-webkit-scrollbar {
    width: 8px;
    background: var(--sec1);
    border: 3px black;
  }

  body::-webkit-scrollbar-thumb {
    background: var(--sec1_2);
    border-radius: 50px;
  }

  body::-webkit-scrollbar-thumb:hover {
    background: var(--sec3);
  }
}

/* =================== MEDIA QUERIES (SMALL DEVICES) =================== */
@media screen and (max-width: 600px) {
  body {
    font-size: 16px;
  }

  .car-item-dist {
    height: 27vh;
  }

  .car-item-dist-low {
    height: 27vh;
  }

  .separador {
    background-color: var(--sec2);
    text-align: left;
    padding-top: 7rem;
    padding-bottom: 7rem;
    font-size: 15px;
  }
}
