// NAVBAR BÁSICO -------------------------------------------------
.navbar-dark {
  position: absolute;
  background-color: #014751;
  --bs-navbar-color: white !important;
  --bs-navbar-background-color: blue;
  --bs-navbar-hover-color: var() !important;
}

// BRAND IMAGE ----------------------------------------------------
.brand-img {
  height: 70px;
  margin-right: 80px;
}

// LINKS NAVS -----------------------------------------------------
.nav-p {
  position: fixed !important;
  width: 100%;
}

.nav-item-p {
  margin-right: 10px;
  vertical-align: middle;
}

._link-nav {
  color: white;
  max-width: 50%;
  margin: 10px auto;
  text-align: center;
}

._link-nav:hover {
  color: var(--sec3);
}

/* =================== MEDIA QUERIES (MEDIUM DEVICES) =================== */
@media screen and (min-width: 1024px) {
  .brand-img {
    margin-left: 100px;
  }

  .contacto-p {
    margin-right: 100px;
  }
}

@media screen and (max-width: 600px) {
  .bg-dark {
    min-height: 2em;
  }

  .brand-img {
    width: 96px !important;
    height: 40px !important;
  }

  .brand-img-2 {
    width: 151px;
    height: 40px;
  }
}

.searchform {
  width: 330px;
  margin-left: 5px !important;
}

.searchbar {
  font-size: 0.875rem /* 14px */;
  line-height: 1.25rem /* 20px */;
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 10px;

  height: 35px;

  border-radius: 17px !important;

  outline: 2px solid transparent;
  outline-offset: 2px;

  border-style: none;
}
